*{
  transition:all .30s ease-in;
}

body{
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-flex-box:wrap;
  display:-ms-flexbox;
  display:flex;
  flex-wrap:wrap;
  justify-content: center;
  align-items:center;
  height:100vh;
  width:100vw
}

#root{
  width:100vw;
  height: 100vh;
}
.container{
}
div.card{
  width:350px;
  padding:10px 40px;
  background:rgb(43, 42, 42);
  color:white;
  margin-bottom:10px;

}

.remove{
  color:lightseagreen;
  font-weight:700;
  height:20px;
}
.names{
  width:auto;
  display:flex;
  margin-top:20px;
  padding:5px;
  flex-direction:column;
  align-items: center;
  max-height:300px;
  overflow-y:auto;
  box-shadow:0px 5px 40px -10px grey;
  justify-content:space-between;
  background-color: lavender;
}
.names::-webkit-scrollbar{
  width:0;
}

.names .sticky{
  position: sticky;
  background:rgb(145, 94, 28);
  width:100%;
  text-align:center;
  padding:5px;
  margin-bottom:10px;
}
.names h5{
  width:100%;
  text-align:center;
  
}
.wrapper{
  width:500px;
  padding:5px 20px;
  display:flex;
  border-radius: 5px;
  justify-content:space-between;
  background-color: rgb(255, 255, 255);
  margin-bottom:10px;
}

.wrapper
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul{
  list-style: none;
  padding:40px 10px;
}
li{
  padding:10px 20px;
  background:lightgrey;
  margin-right:10px;
  margin-bottom:20px;
  border-radius: 10px;
}
img{
  display:block;
  width:40px;
  height:40px;
  filter: brightness(120%);
  border-radius:50%;
}

.card button{
  color:white;
  margin-top:20px;
  font-weight:bold;
}
.container button{
  margin-right:20px;
  color:lightseagreen;
  font-weight:700;
}
.card button:hover{
  color:bisque
}

img.click{
  position:absolute;
  width:300px;
  height:auto;
  /* top:50%;
  left:50%;
  transform:translate(-50%,-50%); */
  height:initial;
  z-index:100;
}

@media (min-width:800px){
  .container{
    display:grid;
    grid-template-columns: repeat(4,1fr);
    max-width:100%;
    padding: 0;
    height:100vh;
  }
  .container .navbar{
    grid-column:1/1;
    background:grey;
  }

  .grid2{
    display:flex;
    grid-column:2/5;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
}